const clonedeep = require('lodash.clonedeep')

export const setState = (prevState, newState) => Object.assign(prevState, newState)

export const scrollWindowToPlant = (state, plantToScroll) => {
    document.getElementById(plantToScroll).scrollIntoView();
}

export const changeDifficulty = (state, difficulty) => {
    state.gameState.currentDifficulty = difficulty
    Object.values(state.gameState.plants).forEach(plant => {

        // Difficulty seedsPerPlantBuffer:

        // Checking if seedsPerPlantBuffer won't make the number of seeds per plant lower than 2 or higher than 5

        let bufferIterations = state.gameState.difficulties[state.gameState.currentDifficulty].seedsPerPlantBuffer


        if (bufferIterations < 0) {

            // If seedPerPlantBuffer is negative:

            // Repeat for "bufferIterations" times while seedsPerPlant is bigger than 2

            while ((plant.seedsPerPlant - 1) >= 2 && bufferIterations < 0) {
                plant.seedsPerPlant--
                bufferIterations++
            }

        } else {

            // If seedPerPlantBuffer is positive or 0

            // Repeat for "bufferIterations" times while seedsPerPlant is lower than 5

            while ((plant.seedsPerPlant + 1) <= 5 && bufferIterations > 0) {
                plant.seedsPerPlant++
                bufferIterations--
            }

        }

        // Difficulty growthTimeBuffer:

        plant.growthTime = Math.round(plant.growthTime * state.gameState.difficulties[state.gameState.currentDifficulty].growthTimeBuffer)

    });
    document.location.reload()
}

export const plantSeed = (state, plant) => {
    if (!state.gameState.difficulties[state.gameState.currentDifficulty].noSeedCost) {
        state.gameState.stock[plant].seedAmount--
    }

    for (let index = 0; index < state.gameState.plants[plant].plantsPerSeed; index++) {

        state.gameState.garden.plants.push(clonedeep(state.gameState.plants[plant]))
    }
}

export const waterPlant = (state, index) => {
    state.gameState.garden.plants[index].wateredTime += (10 * state.gameState.difficulties[state.gameState.currentDifficulty].waterTimeBuffer)
}

export const makeSeed = (state, plant) => {

    state.gameState.stock[plant].plantAmount--
    state.gameState.stock[plant].seedAmount += state.gameState.plants[plant].seedsPerPlant
}

export const removePlant = (state, index) => {
    state.gameState.garden.plants.splice(index, 1)
}

export const mixSeeds = (state, { mainPlant, secondaryPlant }) => {

    if (!state.gameState.difficulties[state.gameState.currentDifficulty].noSeedCost) {
        state.gameState.stock[mainPlant].seedAmount -= 1
        state.gameState.stock[secondaryPlant].seedAmount -= 1
    }

    // Testing for failure (random chance)

    if (state.gameState.difficulties[state.gameState.currentDifficulty].mixFailChance > Math.floor(Math.random * 100)) {

        // On Fail

        alert('UNLUCKY! Your plant mixing failed due to an unlucky specimen')

    } else {

        // On Sucess

        const result = state.gameState.plantCombinations[mainPlant][secondaryPlant]

        // Checking if there is result for mixture

        if (result === '') {

            // If there isn't a result

            const log = {
                main: state.gameState.plants[mainPlant].label,
                secondary: state.gameState.plants[secondaryPlant].label,
                result: ''
            }
            for (let index = 0; index < state.gameState.plantCombinationsLog.length; index++) {
                if (state.gameState.plantCombinationsLog[index] === log) {
                    return 0
                }
            }

            state.gameState.plantCombinationsLog.push(log)

        } else {

            // If there is a result

            state.gameState.stock[result].seedAmount += 1

            const log = {
                main: state.gameState.plants[mainPlant].label,
                secondary: state.gameState.plants[secondaryPlant].label,
                result
            }
            for (let index = 0; index < state.gameState.plantCombinationsLog.length; index++) {
                if (state.gameState.plantCombinationsLog[index].main === log.main && state.gameState.plantCombinationsLog[index].secondary === log.secondary) {
                    return 0
                }
            }

            state.gameState.plantCombinationsLog.push(log)
        }

    }
}

export const clearCombinationLog = (state) => { state.gameState.plantCombinationsLog = [] }

export const removeCombinationLogLine = (state, index) => { state.gameState.plantCombinationsLog.splice(index, 1) }

export const plantCombinationHint = (state) => {

    let foundValidCombination = false

    while (!foundValidCombination) {

        // Main plant chosen at random for mixing test

        const mainPlant = Object.keys(state.gameState.plants)[Math.floor((Math.random()) * (Object.values(state.gameState.plants).length))]

        // Secondary plant chosen at random for mixing test

        const secondaryPlant = Object.keys(state.gameState.plants)[Math.floor((Math.random()) * (Object.values(state.gameState.plants).length))]

        // If combination has a result (a plant result, not an empty string) show that hint...

        if (state.gameState.plantCombinations[mainPlant][secondaryPlant] != '') {

            // Ends loop when a combination is found, sending that combination to the the hint slot 

            state.gameState.currentPlantHint = `${mainPlant} + ? = ${state.gameState.plantCombinations[mainPlant][secondaryPlant]} `

            foundValidCombination = true

        }


    }


}

export const updateGame = (state) => {

    // Runs every second, so everything is updated correctly, plant times, garden slots, and others

    state.gameState.garden.plants.forEach((plant, index) => {

        if (state.gameState.difficulties[state.gameState.currentDifficulty].plantDeathByDrought) {

            if (plant.droughtTime <= 0) {
                alert(`Your ${plant.label} dried to death!`)
                state.gameState.garden.plants.splice(index, 1)
            }

            if (plant.wateredTime <= 0) {
                plant.droughtTime--
            } else {
                state.gameState.garden.plants[index].wateredTime--
                plant.droughtTime = state.gameState.difficulties[state.gameState.currentDifficulty].plantDeathByDroughtTime
            }
        }

        if (plant.growthTime <= 0) {

            state.gameState.stock[plant.name].plantAmount++
            state.gameState.garden.plants.splice(index, 1)


        } else {
            plant.growthTime--

            if (plant.growthTime != 0 && state.gameState.garden.plants[index].wateredTime > 0 && !state.gameState.difficulties[state.gameState.currentDifficulty].plantDeathByDrought) {
                plant.growthTime--
                state.gameState.garden.plants[index].wateredTime--
            }
        }


    });

    let totalSlots = 0

    Object.values(state.gameState.stock).forEach(plant => {
        if (plant.seedAmount > 0 && plant.found === false) {
            plant.found = true
        }

        if (plant.found === true) {
            totalSlots++
        }
    });

    state.gameState.garden.totalSlots = 6 + totalSlots
}