export const getDifficulties = (state) => state.gameState.difficulties

export const getCurrentDifficulty = (state) => state.gameState.currentDifficulty

export const getCurrentPlantHint = (state) => state.gameState.currentPlantHint

export const getPlants = (state) => state.gameState.plants

export const getGarden = (state) => state.gameState.garden

export const getNumberOfAvailableSlots = (state) => state.gameState.garden.totalSlots - state.gameState.garden.plants.length

export const getNumberOfUniquePlantsFound = (state) => {
    let numberOfUniqueFoundPlants = 0

    Object.values(state.gameState.stock).forEach(plant => {
        if (plant.found) {
            numberOfUniqueFoundPlants++
        }
    });

    return numberOfUniqueFoundPlants
}
export const getPlantCombinationLog = (state) => state.gameState.plantCombinationsLog

export const getStock = (state) => state.gameState.stock