<template>
  <div>
    <h2>Seed Mixing</h2>
    <br />
    <br />

    <!-- Seed Mixing -->

    <div class="container">
      <button
        :disabled="
          mainPlant === '' ||
          secondaryPlant === '' ||
          (stock[mainPlant].seedAmount <= 1 &&
            mainPlant === secondaryPlant &&
            !difficulties[currentDifficulty].noSeedCost) ||
          (stock[mainPlant].seedAmount <= 0 &&
            !difficulties[currentDifficulty].noSeedCost) ||
          (stock[secondaryPlant].seedAmount <= 0 &&
            !difficulties[currentDifficulty].noSeedCost)
        "
        @click="mixSeeds"
        class="btn btn-primary"
      >
        Mix Seeds
      </button>
      <table class="table">
        <thead>
          <tr>
            <th scope="col" style="width: 50%" />
            <th scope="col" style="width: 50%" />
          </tr>
        </thead>
        <tbody>
          <tr>
            <th>
              <div class="dropdown">
                <button
                  class="btn btn-success dropdown-toggle"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Main Plant
                </button>
                <ul class="dropdown-menu">
                  <li
                    v-for="(plant, index) in stock"
                    :key="`${index}-${plant.label}`"
                  >
                    <a
                      v-if="plant.name === mainPlant && plant.found"
                      class="dropdown-item active"
                      >{{ plant.label }}</a
                    >
                    <a
                      v-if="plant.name != mainPlant && plant.found"
                      class="dropdown-item"
                      @click="mainPlant = plant.name"
                      >{{ plant.label }}</a
                    >
                  </li>
                </ul>
                <br />
                <br />
                <h5 class="fw-bold" v-if="mainPlant != ''">
                  {{ plants[mainPlant].label }}
                </h5>
              </div>
            </th>
            <td>
              <div class="dropdown">
                <button
                  class="btn btn-success dropdown-toggle"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Secondary Plant
                </button>
                <ul class="dropdown-menu">
                  <li
                    v-for="(plant, index) in stock"
                    :key="`${index}-${plant.label}`"
                  >
                    <a
                      v-if="plant.name === secondaryPlant && plant.found"
                      class="dropdown-item active"
                      >{{ plant.label }}</a
                    >
                    <a
                      v-if="plant.name != secondaryPlant && plant.found"
                      class="dropdown-item"
                      @click="secondaryPlant = plant.name"
                      >{{ plant.label }}</a
                    >
                  </li>
                </ul>
                <br />
                <br />
                <h5 class="fw-bold" v-if="secondaryPlant != ''">
                  {{ plants[secondaryPlant].label }}
                </h5>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Seed Mixing Log -->

    <div
      v-for="(combination, index) in plantCombinationLog"
      :key="`${combination}-${index}`"
    >
      <h5>
        {{ combination.main }} + {{ combination.secondary }} =
        <span
          v-if="combination.result != ''"
          style="cursor: pointer"
          @click="scrollWindowToPlant(combination.result)"
        >
          {{ plants[combination.result].label }}
        </span>

        <span v-if="combination.result === ''"> Nothing </span>
        <span
          style="cursor: pointer"
          class="fs-4 text-danger"
          @click="removeCombinationLogLine(index)"
        >
          X
        </span>
      </h5>
    </div>

    <br />
    <br />

    <button
      v-if="plantCombinationLog.length > 0"
      @click="clearCombinationLog"
      class="btn btn-danger"
    >
      Clear Combination Log
    </button>

    <br />
    <br />
  </div>
</template>

<script>
export default {
  data: () => ({
    mainPlant: "",
    secondaryPlant: "",
  }),

  computed: {
    plants() {
      return this.$store.getters.getPlants;
    },

    garden() {
      return this.$store.getters.getGarden;
    },

    numberOfAvailableSlots() {
      return this.$store.getters.getNumberOfAvailableSlots;
    },

    stock() {
      return this.$store.getters.getStock;
    },

    plantCombinationLog() {
      return this.$store.getters.getPlantCombinationLog;
    },

    difficulties() {
      return this.$store.getters.getDifficulties;
    },

    currentDifficulty() {
      return this.$store.getters.getCurrentDifficulty;
    },
  },

  methods: {
    mixSeeds() {
      if (
        (this.stock[this.mainPlant].seedAmount === 2 &&
          this.mainPlant === this.secondaryPlant &&
          !this.difficulties[this.currentDifficulty].noSeedCost) ||
        (this.stock[this.mainPlant].seedAmount === 1 &&
          !this.difficulties[this.currentDifficulty].noSeedCost) ||
        (this.stock[this.secondaryPlant].seedAmount === 1 &&
          !this.difficulties[this.currentDifficulty].noSeedCost)
      ) {
        // If seed is the last remaining seed, confirm it's usage

        if (confirm("Are you sure you want to spend your last seed?")) {
          this.$store.dispatch("mixSeeds", {
            mainPlant: this.mainPlant,
            secondaryPlant: this.secondaryPlant,
          });
          this.mainPlant = "";
          this.secondaryPlant = "";
        }
      } else {
        // If it is not the last seed, use it

        this.$store.dispatch("mixSeeds", {
          mainPlant: this.mainPlant,
          secondaryPlant: this.secondaryPlant,
        });
        this.mainPlant = "";
        this.secondaryPlant = "";
      }
    },

    clearCombinationLog() {
      this.$store.dispatch("clearCombinationLog");
    },

    removeCombinationLogLine(index) {
      this.$store.dispatch("removeCombinationLogLine", index);
    },

    scrollWindowToPlant(plant) {
      this.$store.dispatch("scrollWindowToPlant", plant);
    },
  },
};
</script>

<style scoped>
button {
  width: 200px;
}
</style>