<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <div class="container-fluid">
        <img src="../../public/favicon.png" />
        <router-link
          style="margin-left: 10px"
          class="navbar-brand"
          :to="{ name: 'game' }"
          >The Gardener</router-link
        >

        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div class="navbar-nav">
            <router-link
              :class="['nav-link', $route.name === 'game' ? 'active' : '']"
              :to="{ name: 'game' }"
              >Game</router-link
            >
            <router-link
              :class="['nav-link', $route.name === 'tutorial' ? 'active' : '']"
              :to="{ name: 'tutorial' }"
              >Tutorial</router-link
            >
            <router-link
              :class="['nav-link', $route.name === 'almanac' ? 'active' : '']"
              :to="{ name: 'almanac' }"
              >Almanac</router-link
            >
            <router-link
              :class="['nav-link', $route.name === 'news' ? 'active' : '']"
              :to="{ name: 'news' }"
              >News</router-link
            >
            <div class="dropdown">
              <button
                :class="`btn btn-outline-${difficulties[currentDifficulty].color} dropdown-toggle`"
                style="margin-left: 10px"
                type="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                {{ difficulties[currentDifficulty].label }}
              </button>
              <ul class="dropdown-menu">
                <li
                  v-for="(difficulty, index) in difficulties"
                  :key="`${index}-${difficulty.label}`"
                >
                  <a
                    v-if="difficulty.name === currentDifficulty"
                    class="dropdown-item active"
                    >{{ difficulty.label }}</a
                  >
                  <a
                    v-if="difficulty.name != currentDifficulty"
                    class="dropdown-item"
                    @click="changeDifficulty(difficulty.name)"
                    >{{ difficulty.label }}</a
                  >
                </li>
              </ul>
              <br />
            </div>

            <button
              class="btn btn-outline-danger"
              style="margin-left: 10px"
              type="reset"
              @click="resetState"
            >
              Reset
            </button>
          </div>
        </div>
      </div>
    </nav>
    <br />
  </div>
</template>

<script>
export default {
  methods: {
    resetState() {
      if (confirm("Are you sure you want to reset your progress?")) {
        this.$store.dispatch("resetState");
      }
    },

    changeDifficulty(difficulty) {
      if (
        confirm(
          "Doing this will reset your progress, are you sure you want to do it?"
        )
      ) {
        this.$store.dispatch("changeDifficulty", difficulty);
      }
    },

    goToTutorial() {
      this.$router.push({ name: "tutorial" });
    },

    goToAlmanac() {
      this.$router.push({ name: "almanac" });
    },
  },

  computed: {
    difficulties() {
      return this.$store.getters.getDifficulties;
    },

    currentDifficulty() {
      return this.$store.getters.getCurrentDifficulty;
    },
  },
};
</script>
